@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,500,900);
.tile {
  line-height: 1;
  text-align: center;
  display: block;
  background-color: transparent;
  border-radius: 0.4rem;
  font-weight: 900;
  font-size: 1.2rem;
  font-family: "Roboto", sans-serif;
  cursor: pointer;
  color: transparent !important;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  transition: transform 100ms cubic-bezier(0.7, -0.01, 0.39, 1); }
  .tile.zero {
    background-color: transparent;
    opacity: 0; }

.wrap-box {
  position: relative;
  width: 100%;
  height: 100%;
  display: grid;
  grid-gap: 0.33333rem; }
  @media screen and (max-width: 768px) {
    .wrap-box {
      grid-gap: 0.5rem; } }

.page {
  position: relative;
  height: 100vh;
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center; }
  .page > div {
    flex: 1 1 30%; }
    @media screen and (max-width: 768px) {
      .page > div {
        flex: 1 1 100%; } }

.wrap-pz {
  position: relative;
  max-width: 420px;
  margin: auto;
  text-align: center; }
  .wrap-pz .body-wrap {
    position: relative;
    max-width: 420px;
    padding: 0.33333rem;
    background-color: #333333;
    border-radius: 0.4rem;
    margin-bottom: 1rem; }
    .wrap-pz .body-wrap .header {
      text-align: center;
      display: block;
      padding: 1rem;
      margin-bottom: 1rem;
      color: #cccccc;
      background-color: #4d4d4d;
      border-radius: 0.4rem; }
  .wrap-pz .btn {
    background-image: linear-gradient(to right, #98698e 0%, #ad87a5 100%);
    border-radius: 40px;
    box-sizing: border-box;
    color: #00a84f;
    display: block;
    font: 1.125rem 'Oswald', Arial, sans-serif;
    /*18*/
    height: 64px;
    letter-spacing: 1px;
    margin: 0 auto;
    padding: 4px;
    position: relative;
    text-decoration: none;
    text-transform: uppercase;
    z-index: 2;
    border: 0;
    width: 64px;
    outline: none;
    cursor: pointer; }
    .wrap-pz .btn img {
      opacity: 0.5;
      max-width: 32px; }
    .wrap-pz .btn:hover {
      color: #fff; }
      .wrap-pz .btn:hover span {
        background: transparent; }
    .wrap-pz .btn span {
      align-items: center;
      background: #7a5472;
      border-radius: 40px;
      display: flex;
      justify-content: center;
      height: 100%;
      transition: background .5s ease;
      width: 100%; }

html, body, div, span, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
abbr, address, cite, code,
del, dfn, em, img, ins, kbd, q, samp,
small, strong, sub, sup, var,
b, i,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, figcaption, figure,
footer, header, hgroup, menu, nav, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent; }

body {
  line-height: 1.2;
  background-color: #1a1a1a;
  color: #000000;
  font-family: "Roboto", sans-serif;
  -webkit-user-select: none;
  /* Chrome all / Safari all */
  -moz-user-select: none;
  /* Firefox all */
  -ms-user-select: none;
  /* IE 10+ */
  user-select: none; }

* {
  box-sizing: border-box;
  -webkit-box-sizing: border-box; }

article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

nav ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

a {
  margin: 0;
  padding: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent; }

/* change colours to suit your needs */
ins {
  background-color: #ff9;
  color: #000;
  text-decoration: none; }

/* change colours to suit your needs */
mark {
  background-color: #ff9;
  color: #000;
  font-style: italic;
  font-weight: bold; }

del {
  text-decoration: line-through; }

abbr[title], dfn[title] {
  border-bottom: 1px dotted;
  cursor: help; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

/* change border colour to suit your needs */
hr {
  display: block;
  height: 1px;
  border: 0;
  border-top: 1px solid #cccccc;
  margin: 1em 0;
  padding: 0; }

input, select {
  vertical-align: middle; }

